* {
  box-sizing: border-box;
}

body {
  font-family: "Montserrat", sans-serif;
  background-color: #f9f9f9;
  font-size: 16px;
}

img {
  max-width: 190px;
  border-radius: 10%;
  margin-left: auto;
  margin-right: auto;
  display: block;
}

h1 {
  font-size: 2.2em;
  font-weight: 700;
  margin-bottom: 5px;
  margin-top: 0;
}

h2 {
  font-size: 1.2em;
  font-weight: 700;
}

h3 {
  font-size: 0.9em;
  font-weight: 600;
  margin: 0;
}

section>h3 {
  margin-top: 2em;
}

h4 {
  font-weight: 400;
  font-size: 0.8em;
  margin-top: 5px;
}

p {
  font-size: 0.9em;
  line-height: 1.5em;
  font-weight: 400;
  color: rgb(85, 83, 83);
}

.right__heading {
  text-transform: uppercase;
  color: #ffffff;
  font-size: 1.1em;
  font-weight: 600;
}

ul {
  padding-left: 1em;
}

a {
  text-decoration: none;
  color: rgb(85, 83, 83);
  font-size: 0.9em;
  line-height: 1.5em;
  font-weight: 400;
  transition: all .5s;

  &:hover {
    color: #05055a;
  }
}

section {
  margin-bottom: 3em;
}

.container {
  display: flex;
  max-width: 900px;
  width: 100%;
  flex-direction: row;
  margin-left: auto;
  margin-right: auto;
  background-color: #fff;
}

.right {
  background-color: #05055a;
  width: 38.11%;
  color: #fff;
  padding: 3em;

  &>img {
    margin-bottom: 3em;
  }
}

.left {
  width: 61.77%;
  flex: 1;
  color: #05055a;
  padding: 3em;
}

.heading__section {
  padding-left: 2em;
  margin-bottom: 3em;

  i {
    color: #fff;
    font-size: 1.5em;
    width: 30px;
    margin-right: 0.5em;
    text-align: center;
    line-height: 1.5em;
  }
}

.skill__item {
  display: flex;
  flex-direction: row;
  align-items: center;
  margin-bottom: 1em;

  p {
    margin-top: 5px;
    margin-bottom: 5px;
  }

  .sub {
    color: #9b9ba1;
    text-transform: uppercase;
    font-size: 0.8em;
    letter-spacing: 0.02;
    font-weight: 600;
  }
}

.skill__section {
  padding-left: 0;

  p {
    display: flex;
    align-items: center;
    font-size: 1em;
    line-height: 1em;
    color: #fff;
  }
}

.right__subheading {
  text-transform: uppercase;
  color: #9b9ba1;
  font-size: 0.9em;
  font-weight: 700;
  margin-bottom: 5px;
}

.right__text {
  color: #fff;
  font-size: 0.9em;
  line-height: 1.5em;
  font-weight: 400;
  margin: 0 0 1em 0;
}

.signature {
  p {
    font-size: 0.7em;
  }
}